import { useState } from "react";
import ColorBlock from "./ColorBlock";
import "./styles.css";

function App() {
  const [color, setColor] = useState({ color: "" });
  function handle(colorValue) {
    setColor({ color: colorValue });
  }
  return (
    <div className="App" style={{ backgroundColor: color.color }}>
      <ColorBlock color="red" handle={handle} />
      <ColorBlock color="green" handle={handle} />
      <ColorBlock color="blue" handle={handle} />
      <ColorBlock color="yellow" handle={handle} />
      <ColorBlock color="white" handle={handle} />
    </div>
  );
}
export default App;
